.social li {
    display: inline-block;
    padding: 10px;
}

.social li a {
    width: 67px;
    height: 52px;
    display: block;
    border: 1px solid blue;
    text-align: center;
    line-height: 30px;
    border-radius: 4px;
}

.paddingLeft {
    padding-left: 20px;
}

.contact {
    list-style: none;
    font-style: italic;
    font-weight: bold; 
}

.contact li {
    display: block;
    padding: 10px;
    position: relative;
}

.contact li i {
    width: 32px;
    height: 32px;
    text-align: center;
    line-height: 30px;
    border-radius: 4px;
}

.rectangleFooter {
    border: 1px solid green;
    padding: 20px;
}

.fa.fa-instagram {
    color: transparent;
    background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
    background: -webkit-radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
    background-clip: text;
    -webkit-background-clip: text;
  }
