.topHeaderBox {
    background-color: skyblue;
}

.topHeaderBox ul {
    list-style: none;
}

.topHeaderBox li {
    display: inline-block;
    padding: 10px 30px;
}

.rectangleTopHeader {
    border: 1px solid black;
    padding-left: 20px;
}

.schoolLogo {
    float: left;
    border-width: 1px;
    border-style: solid;
    margin: 0px;
    width: 265px;
    height: 183px;
}

.schoolName {
    float: left;
    border-width: 1px;
    margin: 0px;
    font-style: italic;
    font-size: 47px;
    color: white;
    text-shadow: 2px 2px darkorange;
}

.schoolAddress {
    float: left;
    border-width: 1px;
    margin: 0px;
    font-style: italic;
    font-size: x-large;
}